import React, { Component } from 'react'
import { connect } from 'react-redux'

import './Privacy.css'
import { initialisePrivacy } from './PrivacyActions'

class Privacy extends Component {
  componentDidMount() {
    this.props.handleInitPrivacy()
  }

  render() {
    return (
      <section className="main__section">
        <h1>Datenschutz</h1>
        <p>Die Nutzung unserer Webseite ist in der Regel ohne Angabe personenbezogener Daten mglich. Soweit auf unseren Seiten personenbezogene Daten (beispielsweise Name, Anschrift oder E-Mail-Adressen) erhoben werden, erfolgt dies, soweit mglich, stets auf freiwilliger Basis. Diese Daten werden ohne Ihre ausdrckliche Zustimmung nicht an Dritte weitergegeben.</p>
        <p>Wir weisen darauf hin, dass die Datenbertragung im Internet (z.B. bei der Kommunikation per E-Mail) Sicherheitslcken aufweisen kann. Ein lckenloser Schutz der Daten vor dem Zugriff durch Dritte ist nicht mglich.<br />Der Nutzung von im Rahmen der Impressumspflicht verffentlichten Kontaktdaten durch Dritte zur bersendung von nicht ausdrcklich angeforderter Werbung und Informationsmaterialien wird hiermit ausdrcklich widersprochen. Die Betreiber der Seiten behalten sich ausdrcklich rechtliche Schritte im Falle der unverlangten Zusendung von Werbeinformationen, etwa durch Spam-Mails, vor.</p>
      </section>
    )
  }
}

const mapDispatchToProps = (dispatch) => ({
  handleInitPrivacy: () => {
    dispatch(initialisePrivacy())
  }
})


export default connect(
  null,
  mapDispatchToProps
)(Privacy)



